<script>

export default {
	name: 'EasterEgg',
	data: () => ({
		containerClass: '',
	}),
	mounted() {
		setTimeout(() => {
			this.containerClass = 'active';
		}, 0);
	},
	methods: {
		closeEasterEgg() {
			this.containerClass = '';

			setTimeout(() => {
				this.$emit('closeEasterEgg');
			}, 1000);
		},
	},
};
</script>

